import * as React from 'react';
import { Link } from 'react-router';


class Menu extends React.Component {

    changeNick(){
        let nick = document.querySelector(".nick").value;
        if(nick.length<1){
            nick = "noName";
        }
        this.props.setNick(nick);
        nick="noName"
    }
  render() {
    if(this.props.nickName=="noName"){
        return (
            <div className="menu">
                <div className="content">
                    <h1>{this.props.tekst}</h1>
                    <input type="Text" maxLength="10" className="nick" placeholder="Enter you'r name"/>
                    <Link to="game"><input type="button" className="start" value="Start" onClick={this.changeNick.bind(this)}/></Link>
                    <Link to="board"><input type="button" className="start" value="Board"/></Link>
                </div>
            </div>
        );
    }
    else
        return (
            <div className="menu">
                <div className="content">
                    <h1>{this.props.tekst}</h1>
                    <Link to="game"><input type="button" className="start" value="Retry"/></Link>
                    <Link to="board"><input type="button" className="start" value="Board"/></Link>
                </div>
            </div>
        );
  }
}

export default Menu;


